.input,
.textarea {
	&.error {
		border-color: $red !important; }
	&.valid {
		border-color: $green; } }

.textarea {
	width: 100%;
	resize: none;
	background-color: $white;
	font-size: $text-reg; //16px
	border: 1px solid #c8cdd5;
	@include font-family($proxima-reg);
	@include placeholder-color($blue-gray);
	border-radius: 4px;
	padding: 20px; }

// Input Style
.input {
	display: inline-block;
	vertical-align: top;
	margin: 0;
	font-size: $text-reg; //16px
	width: 100%;
	border: 1px solid #c8cdd5;
	padding: 16px 20px 16px 19px;
	@include placeholder-color($blue-gray);
	color: $blue-gray;
	transition: border-color .24s ease-in-out;
	border-radius: 4px;
	&.disabled {
		cursor: not-allowed;
		cursor: no-drop;
		@include nosel; }
	&-file {
		font-size: 0;
		border-radius: 4px;
		border: 1px solid #c8cdd5;
		width: 130px;
		height: 130px;
		position: relative;
		display: block;
		float: left;
		overflow: hidden;
		input {
			font-size: 0;
			display: none;
			opacity: 0; }
		&.is-error {
			.is-hidden {
				opacity: 1;
				display: block; }
			.input-file__content {
				padding: 0 15px 0; } }
		&__img {
			display: none;
			position: relative;
			z-index: 4;
			opacity: 0;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center center;
			width: 130px;
			height: 130px;
			transition: opacity .24s ease-in-out;
			&.is-visible {
				display: inline-block;
				opacity: 1; } }
		&__content {
			z-index: 3;
			display: block;
			height: 100%;
			position: relative;
			text-align: center;
			padding: 30px 15px 0 15px;
			cursor: pointer;
			.add {
				@include font-family($proxima-reg);
				font-size: $text-reg; //16px
				color: $blue-gray;
				margin-bottom: 2px; }
			small {
				font-size: $text-xs; //12px
				color: #c2c2c4;
				@include font-family($proxima-reg);
				&.is-hidden {
					opacity: 0;
					color: red;
					transition: opacity .24s ease-in-out;
					display: none; } } }
		&__btn {
			width: 100%;
			height: 100%;
			position: absolute;
			background-color: #f7f7f7;
			top: 0;
			z-index: 2;
			left: 0;
			right: 0;
			bottom: 0;
			display: block; } } }

.has-error {
	position: relative; }
.help-block {
	opacity: 0;
	position: absolute;
	top: 100%;
	left: 0;
	display: block;
	transition: opacity .24s ease-in-out;
	&.form-error {
		font-size: $text-xs; //12px
		color: $red;
		opacity: 1; } }
